:root {
  --text-color: #2a2a2a;
  --accent-color: #05604b;
  --contrast-color: #dc4a23;
  --accent-light-color: #c5e1db;
  --contrast-light-color: #eb9781;
  --bg-body-color: #fff7e8;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: var(--text-color);
  background: var(--bg-body-color);
  font-size: 18px;
  line-height: 1.4em;
  min-width: 360px !important;
}

h1, h2 {
  margin-top: 0;
  margin-bottom: .5rem;
  line-height: 1.2em;
}

h1 {
  text-transform: uppercase;
  font-size: 2.5rem;
  font-weight: 900;
  line-height: 3.5rem;
}

h2 {
  letter-spacing: 1.4px;
  font-size: 2.25rem;
  font-weight: 900;
}

@media (max-width: 768px) {
  body {
    font-size: 1rem;
  }

  h2 {
    letter-spacing: 0;
    font-size: 2rem;
  }
}

p {
  margin-bottom: 1rem;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none !important;
}

hr {
  color: inherit;
  opacity: .25;
  height: 1px;
  background-color: currentColor;
  border: 0;
  margin: 1rem 0;
}

.container {
  max-width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
}

@media (min-width: 576px) {
  .container {
    max-width: 640px;
  }
}

@media (min-width: 768px) {
  .container {
    max-width: 720px;
  }
}

.header {
  width: 100%;
  z-index: 10;
  background-color: #43d41f;
  padding-top: 5px;
  padding-bottom: 5px;
  position: fixed;
  box-shadow: 0 3px 6px 1px #00000040;
}

.nav-main .container {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.nav-logo-header-img {
  max-height: 150px;
}

.nav-logo-footer-img {
  max-height: 100px;
}

.nav-menu {
  justify-content: center;
  display: flex;
}

.nav-menu .nav-item {
  margin-right: 15px;
  font-size: 1.5rem;
  font-weight: 600;
}

.nav-menu .nav-item.active {
  border-bottom: 1px dashed #efe5e5;
  font-weight: 900;
}

.nav-menu .nav-link {
  color: #000;
  padding: 5px;
  line-height: 1em;
}

.nav-menu .nav-link:hover {
  color: var(--accent-color);
}

@media (min-width: 768px) {
  .header {
    padding-top: 10px;
    padding-bottom: 10px;
  }

  .nav-main .container {
    flex-direction: row;
    justify-content: space-between;
    display: flex;
  }
}

footer {
  color: #000;
  background-color: #43d41f;
  margin: 0;
  padding: 30px 0 40px;
}

footer hr {
  border-bottom: 2px solid #000;
}

.footer-top, .footer-bottom, .footer-social {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

footer a:hover {
  opacity: .7;
  color: var(--accent-color);
}

.footer-top p {
  margin-bottom: 10px;
  font-weight: 600;
}

.footer-bottom {
  font-size: .82rem;
}

.footer-bottom p {
  margin-bottom: 0;
}

.footer-menu {
  align-items: center;
  margin-bottom: 0;
  display: flex;
}

.footer-menu li {
  margin-right: 15px;
}

.footer-menu li:last-child {
  margin-right: 0;
}

@media (min-width: 768px) {
  .footer-top p {
    max-width: 300px;
    margin-bottom: 0;
  }

  .footer-top, .footer-bottom, .footer-social {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }

  .footer-menu li {
    margin-left: 15px;
    margin-right: 0;
  }
}

@media (max-width: 767px) {
  .footer-social p {
    margin-bottom: 5px;
    font-size: 1rem;
    font-weight: 400;
  }
}

main {
  padding-top: 200px;
}

#videos .grid-container {
  margin-bottom: 50px;
}

#videos .grid-container .item-video {
  background-color: #fff;
  margin-bottom: 24px;
}

#videos .grid-container .item-video h2 {
  text-transform: uppercase;
  padding: 15px 15px 0;
  font-size: 1.2rem;
}

@media (min-width: 768px) {
  #videos .grid-container {
    grid-gap: 24px;
    grid-template-columns: 1fr 1fr;
    display: grid;
  }
}

.centrado {
  text-align: center;
}

.contenedor-video {
  height: 0;
  padding-top: 30px;
  padding-bottom: 56.25%;
  position: relative;
  overflow: hidden;
}

.contenedor-video iframe, .contenedor-video object, .contenedor-video embed {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

